<template>
	<div class="container">
		<v-back :title="$route.query.type == 'set' ? '访客信息修改' : '添加访客'" />
		<div class="scroll-view">
			<md-field>
				<div class="md-field-item is-solid">
					<div class="md-field-item-content" style="padding: 0;">
						<label class="md-field-item-title">* 图片</label>
						<div class="md-field-item-control">
							<ul class="image-reader-list">
								<li
								class="image-reader-item"
								v-for="(img, index) in imageList['tupian']"
								:key="index"
								:style="{
									'backgroundImage': `url(${img})`,
									'backgroundPosition': 'center center',
									'backgroundRepeat': 'no-repeat',
									'backgroundSize': 'cover'
								}"
								@click="imgsShow(img)">
								<md-tag
									class="image-reader-item-del"
									size="small"
									shape="quarter"
									fill-color="#111A34"
									type="fill"
									font-color="#fff"
									@click.native.stop="onDeleteImage('tupian', index)"
								>
									<md-icon name="close"></md-icon>
								</md-tag>
								</li>
								<li class="image-reader-item add" v-show="imageList.tupian.length == 0">
									<md-image-reader
										name="tupian"
										@select="onReaderSelect"
										@complete="onReaderComplete"
										@error="onReaderError"
									>
									</md-image-reader>
									<md-icon name="camera" size="md" color="#CCC"></md-icon>
									<p>添加图片</p>
								</li>
							</ul>
						</div>
					</div>
				</div>
				
				<md-input-item
				  ref="input0"
				  title="* 姓名"
				  placeholder="请输入姓名"
				  is-amount
				  :maxlength="15"
				  v-model="visitor_name">
				</md-input-item>
				
				<md-field-item title="* 门禁开启" :content="start_time" @click="showSelector1" arrow solid />
				<md-date-picker
				    ref="datePicker"
				    v-model="isSelectorShow1"
				    type="datetime"
				    title="选择日期"
				    large-radius
				    :default-date="currentDate1"
				    @confirm="onSelectorConfirm1" >
				</md-date-picker>
				
				<md-field-item title="* 门禁关闭" :content="end_time" @click="showSelector2" arrow solid />
				<md-date-picker
				    ref="datePicker2"
				    v-model="isSelectorShow2"
				    type="datetime"
				    title="选择日期"
					:min-date="minDate"
				    large-radius
				    :default-date="currentDate2"
				    @confirm="onSelectorConfirm2" >
				</md-date-picker>
				
				<div class="md-field-item is-solid">
					<div class="md-field-item-content" style="padding: 0;">
						<label class="md-field-item-title">* 访问楼栋</label>
						<div class="md-field-item-control">
							<div class="md-example-child md-example-child-drop-menu md-example-child-drop-menu-0">
								<md-check-group v-model="insurants">
								  <md-check-box :name="item.buildingId" v-for="(item, index) in floors" :key="index">{{item.buildingName}}</md-check-box>
								</md-check-group>
							</div>
						</div>
					</div>
				</div>
				
				<div class="md-field-item is-solid">
					<div class="md-field-item-content" style="padding: 0;">
						<label class="md-field-item-title">访客类型</label>
						<div class="md-field-item-control">
							<div class="md-example-child md-example-child-drop-menu md-example-child-drop-menu-0">
								<md-drop-menu :data="data" :default-value="array"/>
							</div>
						</div>
					</div>
				</div>
				
				<div class="md-field-item is-solid">
					<div class="md-field-item-content" style="padding: 0;">
						<label class="md-field-item-title">来访目的</label>
						<div class="md-field-item-control">
							<div class="md-example-child md-example-child-drop-menu md-example-child-drop-menu-0">
								<md-drop-menu :data="data2" :default-value="array2"/>
							</div>
						</div>
					</div>
				</div>
				
				<md-input-item
				  ref="input1"
				  title="所属单位"
				  placeholder="0-40个字符"
				  is-amount
				  :maxlength="40"
				  v-model="attach_department">
				</md-input-item>
				
				<md-input-item
				  ref="input2"
				  title="联系方式"
				  placeholder="0-40个字符"
				  is-amount
				  :maxlength="40"
				  v-model="phone">
				</md-input-item>
				
				<md-input-item
				  ref="input3"
				  title="受访人"
				  placeholder="0-40个字符"
				  is-amount
				  :maxlength="40"
				  v-model="interviewee">
				</md-input-item>
				
				<md-input-item
				  ref="input6"
				  title="备注"
				  placeholder="0-40个字符"
				  is-amount
				  :maxlength="40"
				  v-model="remark">
				</md-input-item>
				
				
			</md-field>
			<md-field>
				<md-button type="primary" size="small" round @click="submit">提交保存</md-button>
			</md-field>
		</div>
		<!-- 图片预览 -->
		<md-image-viewer
			v-model="isViewerShow"
			:list="imgs"
			:has-dots="true">
		</md-image-viewer>
	</div>
</template>

<script>
	import axios from 'axios'
	import compressImage from 'ls-compressimage'
	export default {
		name: 'add',
		data() {
			return {
				isViewerShow: false,
				imgs: [],
				id: '',
				userId: '',
				usernbr: '',
				visitor_name: '',//姓名
				visitor_type: '',//访客类型
				visit_purpose: '',//来访目的
				attach_department: '',//所属单位
				phone: '',//联系方式
				interviewee: '',//受访人
				start_time: '',//门禁开启
				end_time: '',//门禁关闭
				remark: '',//备注
				visitor_photo: '', //图片
				imageList: {
				    tupian: []
				},
				array: ['1'],
				data: [
					{
						text: '请选择',
						options: [
							{
							  value: '1',
							  text: '普通访客',
							},
							{
							  value: '2',
							  text: 'vip访客',
							},
						],
					}
				],
				array2: ['0'],
				data2: [
					{
						text: '请选择',
						options: [
							{
							  value: '0',
							  text: '其他',
							},
							{
							  value: '1',
							  text: '面试',
							},
							{
							  value: '2',
							  text: '商务',
							},
							{
							  value: '3',
							  text: '亲友',
							},
							{
							  value: '4',
							  text: '快递发货',
							},
						],
					}
				],
				blob: null,
				file: {},
				isSelectorShow1: false,
				isSelectorShow2: false,
				currentDate1: new Date('2020/05/02 15:32'),
				currentDate2: new Date('2020/06/02 15:32'),
				insurants: [],
				floors: []
			}
		},
		computed: {
			minDate() {
				return this.start_time ? new Date(this.start_time) : new Date()
			}
		},
		methods: {
			imgsShow(img) {
				this.isViewerShow = true
				this.imgs = [img]
			},
			showSelector1() {
			    this.isSelectorShow1 = true
			},
			onSelectorConfirm1() {
			    this.start_time = this.$refs.datePicker.getFormatDate('yyyy/MM/dd HH:mm')
			},
			showSelector2() {
			    this.isSelectorShow2 = true
			},
			onSelectorConfirm2() {
			    this.end_time = this.$refs.datePicker2.getFormatDate('yyyy/MM/dd HH:mm')
			},
			
			
			onReaderSelect(name, {files}) {
			    this.$toast.loading('图片读取中...')
			},
			onReaderComplete(name, {dataUrl, blob, file}) {
			    this.$toast.hide()
			    setTimeout(() => {
			        const demoImageList = this.imageList[name] || []
			        demoImageList.push(dataUrl)
			        this.$set(this.imageList, name, demoImageList)
					this.blob = blob
					this.file = file
			    }, 100)
			},
			onReaderError(name, {msg}) {
			    this.$toast.failed(msg)
			},
			onDeleteImage(name, index) {
			    const demoImageList = this.imageList[name] || []
			    demoImageList.splice(index, 1)
			    this.$set(this.imageList, name, demoImageList)
			},
			submit() {
				let params = {
					visitor_name: this.visitor_name,
					visitor_type: this.array[0],
					visit_purpose: this.array2[0],
					attach_department: this.attach_department,
					phone: this.phone,
					interviewee: this.interviewee,
					start_time: this.start_time,
					end_time: this.end_time,
					remark: this.remark,
					visitor_photo: this.visitor_photo,
					arr: JSON.stringify(this.insurants)
				}
				
				//return;
				if (this.visitor_name == '') {
					this.$toast.failed('请输入姓名')
				} else if (this.start_time == '') {
					this.$toast.failed('请选择门禁开启时间')
				} else if (this.end_time == '') {
					this.$toast.failed('请选择门禁关闭时间')
				} else if (this.imageList.tupian.length == 0) {
					this.$toast.failed('请上传图片')
				} else if (this.insurants.length == 0) {
					this.$toast.failed('请选择可访问楼栋')
				} else {
					if (new Date(this.start_time) >= new Date(this.end_time)) {
						this.$toast.failed('门禁关闭时间不能小于开启时间')
						return;
					}
					if (this.blob == null) {
						this.updateStudent(params)
					} else {
						this.$toast.loading('图片上传中...');
						new compressImage({
							res: this.file, // 必选 资源
							justConversion: false, // 可选 声明了此字段说明只进行格式转换 res requestType responseType callback必填，其他随意
							width: 300, // 可选(justConversion存在可不选) (默认图片自身宽度,建议自定义) 压缩后图片宽度
							//height: 300,//可选 (默认 自定义图片宽度/原生图片宽度*原生图片高度) 压缩后图片高度
							quality: 0.1, // 可选 (默认0.3) 压缩层级质量（0<quality<1）越大压缩越快 图片质量跨度相对低 越小则相反
							size: 500, // 必选(justConversion存在可不选) 压缩后图片大小 单位k
							requestType: 'file', // 必选 传入文件类别 base64 | file | blob
							responseType: 'file', // 必选 返回文件类别 base64 | file | blob
							fileName: 'person', // 如果responseType的类型为file和非justConversion 此选项可选 返回文件对象的文件名
							callback: (result) => {
								if (result === null) {
									this.$toast.failed("图片上传失败")
									return
								}
								console.log(result.size / 1024)
								var formData = new FormData();
								formData.append("file", result, new Date().getTime() + '.' + result.type.split('/')[1]);
								axios.post('http://106.12.3.212/dorm/visitor/UploadImg/-1', formData, { headers: {'Content-Type': 'multipart/form-data'} }).then( res => {
									  if (res.data.code == 0) {
										  this.visitor_photo = res.data.url
										  params.visitor_photo = res.data.url
										  setTimeout(() => {
											this.updateStudent(params)
										  }, 500)
									  } else {
										  this.$toast.failed("图片上传失败")
									  }
								}).catch((err) => {
									this.$toast.failed("图片上传失败")
								})
								/* axios({
									method: 'post',
									url: this.$baseUrl() + '/users/upload/visit',
									data: formData,
									dataType: 'json',
									async: false,
									processData: false,
									contentType: false
								}).then( res => {
									console.log(res.data.data)
									this.visitor_photo = res.data.data
									params.visitor_photo = res.data.data
									setTimeout(() => {
										this.updateStudent(params)
									}, 500)
								}).catch((err) => {
									this.$toast.failed("图片上传失败")
								}) */
							}
						})
					}
					
				}
			},
			updateStudent(params) {
				if (this.$route.query.type == 'set') {
					params.id = this.id
					params.user_id = this.userId
					params.usernbr = this.usernbr
				} else {
					params.usernbr = (new Date()).valueOf()
				}
				this.$toast.loading('数据提交中...');
				this.$post('/users/visit_add', params, ()=>{
				    this.$toast.succeed("提交成功")
				    setTimeout(() => {
				    	this.$router.back()
				    }, 1000)
				})
			}
		},
		mounted() {
			//获取楼栋
			this.$get('/dorm/floor', {
				//
			}, (data) => {
				this.floors = data.data
			})
			if (this.$route.query.type == 'set') {
				document.title = window.configs.headertitle + '访客信息修改'
				this.usernbr = this.$route.query.usernbr
				this.$getL('/users/person_visitor', {
					usernbr: this.$route.query.usernbr
				}, (data)=>{
					this.$toast.hide()
					let user = data.data[0]
					this.id = user.b_visitor.id
					this.userId = user.user_id
					this.visitor_name = user.b_visitor.visitor_name
					this.array = [user.b_visitor.visitor_type]
					this.array2 = [user.b_visitor.visit_purpose]
					this.attach_department = user.b_visitor.attach_department
					this.phone = user.b_visitor.phone
					this.interviewee = user.b_visitor.interviewee
					this.start_time = user.b_visitor.start_time.slice(0, user.b_visitor.start_time.length - 3).replace(/-/g,'/')
					this.currentDate1 = new Date(this.start_time)
					this.end_time = user.b_visitor.end_time.slice(0, user.b_visitor.end_time.length - 3).replace(/-/g,'/')
					this.currentDate2 = new Date(this.end_time)
					this.remark = user.b_visitor.remark
					this.imageList.tupian = user.b_visitor.visitor_photo ? [user.b_visitor.visitor_photo] : []
					this.visitor_photo = user.b_visitor.visitor_photo ? user.b_visitor.visitor_photo : ''
					
					this.insurants = []
					user.person_entry_exit_rights.forEach(item => {
						this.insurants.push(item.entry_exit_building_id)
					})
					
				})
			}
		}
	}
</script>

<style scoped="scoped">
	.image-reader-list {
	     width: 100%;
	     margin-top: 3vw;
		 clear: both;
	 }
	 .image-reader-list .image-reader-item {
	     position: relative;
	     width: 25vw;
	     height: 25vw;
	     padding-bottom: 23.5%;
	     margin-bottom: 2%;
	     margin-right: 2%;
	     background: #FFF;
	     box-shadow: 0 5px 20px rgba(197, 202, 213, .25);
	     box-sizing: border-box;
	     list-style: none;
	     border-radius: 4px;
	     background-size: cover;
	     overflow: hidden;
	 }
	 .image-reader-list .image-reader-item .md-tag {
	     position: absolute;
	     right: 0;
	 }
	 .image-reader-item.add .md-icon {
	     position: absolute;
	     top: 40%!important;
	     left: 50%;
	     transform: translate(-50%, -50%);
	     opacity: .5;
	 }
	.image-reader-item.add p {
	     position: absolute;
	     top: 35%;
	     left: 0;
	     width: 100%;
	     margin-top: 15px;
	     font-size: 22px;
	     color: #CCC;
	     text-align: center;
	     font-size: 12px;
	 }
	 .md-drop-menu {
		 position: relative;
	 }
	 .md-drop-menu >>> .md-popup {
		 z-index: 1201;
	 }
	 .md-drop-menu >>> .md-drop-menu-bar {
		 background: none;
	 }
	 .md-drop-menu >>> .md-drop-menu-bar:before {
		 display: none;
	 }
	 .md-drop-menu >>> .md-drop-menu-bar .bar-item {
		 justify-content: left;
	 }
	 .scroll-view {
		height: calc(100vh - 10vw);
		overflow-y: auto;
	 }
	 .md-check-group {
		 padding-top: 5px;
	 }
	 .md-check-box {
		 margin-right: 5px!important;
		 margin-bottom: 5px;
	 }
	 .md-date-picker >>> .md-picker .md-popup {
		 z-index: 1300;
	 }
</style>
